@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("..//fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1604px;
  margin: 0 auto;
  padding: 0 20px;
}

.title {
  color: #000000;
  font-size: 60px;
  font-weight: 700;
  line-height: 110%;
  text-transform: uppercase;
}
.title--white {
  color: #ffffff;
}
@media (max-width: 1563px) {
  .title {
    font-size: 30px;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 22px;
  }
}

.banner {
  width: 100%;
  min-height: 220px;
  position: relative;
  max-width: 1604px;
  margin: 0 auto;
  padding: 0 20px;
}
.banner__content {
  background: radial-gradient(223.95% 83.78% at 68.75% 50%, #002C43 0%, #000E22 100%);
  padding: 43px 60px 45px 55px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1563px) {
  .banner__content {
    padding: 27px 23px 19px 30px;
  }
}
@media (max-width: 767px) {
  .banner__content {
    padding: 20px 20px 30px 20px;
    flex-direction: column;
    align-items: flex-start;
  }
}
.banner--red .banner__content {
  background: linear-gradient(261deg, #B30505 9.19%, #640606 96.95%);
}
.banner--map {
  margin-bottom: 100px;
}
@media (max-width: 1563px) {
  .banner--map {
    margin-bottom: 60px;
  }
}
@media (max-width: 767px) {
  .banner--map {
    margin-bottom: 40px;
  }
}
.banner__title {
  color: #ffffff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-size: 60px;
  font-weight: 700;
  line-height: 110%;
  text-transform: uppercase;
  max-width: 665px;
  margin: 0;
}
@media (max-width: 1563px) {
  .banner__title {
    font-size: 30px;
    max-width: 333px;
  }
}
@media (max-width: 767px) {
  .banner__title {
    font-size: 24px;
    margin-bottom: 40px;
  }
}
@media (max-width: 359px) {
  .banner__title {
    font-size: 20px;
  }
}
.banner__image {
  position: absolute;
  right: 20%;
  bottom: 0;
  height: 100%;
}
.banner__image img {
  height: 100%;
  object-fit: cover;
}
@media (max-width: 1563px) {
  .banner__image {
    width: 398px;
    height: 112px;
    right: 25%;
    bottom: 23px;
  }
}
@media (max-width: 767px) {
  .banner__image {
    width: 284px;
    height: 166px;
    right: auto;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
  }
}
@media (max-width: 1563px) {
  .banner {
    min-height: 112px;
    padding: 23px 56px 23px 26px;
  }
}
@media (max-width: 767px) {
  .banner {
    min-height: 165px;
    padding: 20px;
  }
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

[data-accordion-init] > [data-tabs=content],
[data-accordion-init] > [data-tabs=controls] {
  display: none;
}
[data-accordion-init] [data-tabs=element] {
  z-index: 1;
  visibility: visible;
  position: static;
  opacity: 1;
}

[data-tabs=accordion-wrapper] {
  display: block;
  max-height: 0;
  transition: max-height 0.3s ease;
  overflow: hidden;
}

[data-tabs=accordion-content] {
  overflow: hidden;
}

[data-tabs=accordion] {
  overflow: hidden;
  position: relative;
  isolation: isolate;
}

[data-tabs=accordion].is-active > [data-tabs=accordion-wrapper] {
  max-height: none;
}

.tabs {
  max-width: 100%;
}
.tabs.enterprise-tabs {
  margin-bottom: 100px;
}
@media (max-width: 1563px) {
  .tabs.enterprise-tabs {
    margin-bottom: 60px;
  }
}
@media (max-width: 767px) {
  .tabs.enterprise-tabs {
    margin-bottom: 40px;
  }
}
.tabs.agro-category-tabs, .tabs.agro-enterprise-tabs {
  margin-bottom: 100px;
}
@media (max-width: 1563px) {
  .tabs.agro-category-tabs, .tabs.agro-enterprise-tabs {
    margin-bottom: 60px;
  }
}
@media (max-width: 767px) {
  .tabs.agro-category-tabs, .tabs.agro-enterprise-tabs {
    margin-bottom: 40px;
  }
}
.tabs.agro-category-tabs .tabs__element, .tabs.agro-enterprise-tabs .tabs__element {
  opacity: 1;
  visibility: visible;
  position: static;
}
.tabs__controls {
  margin: 0 0 20px 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .tabs__controls {
    margin-bottom: 5px;
  }
}
.tabs__title {
  margin: 0 0 50px;
}
.tabs__title .tabs__subtitle {
  color: #951a1a;
}
@media (max-width: 1563px) {
  .tabs__title {
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .tabs__title {
    margin-bottom: 20px;
  }
}
.tabs__control.btn {
  text-transform: uppercase;
  cursor: pointer;
  opacity: 0.5;
  margin: 0;
  min-width: auto;
  justify-content: center;
  padding: 20px 30px;
  background: rgba(255, 255, 255, 0);
  color: #002940;
  border: 1px solid #002940;
  transition: border-color 0.3s ease, opacity 0.3s ease, color 0.3s ease;
  font-size: 30px;
  width: max-content;
  white-space: nowrap;
}
.tabs__control.btn.is-active {
  opacity: 1;
  pointer-events: none;
  border-bottom-color: rgba(255, 255, 255, 0);
}
.tabs__control.btn[aria-selected=true], .tabs__control.btn[aria-selected=true]:hover:not(.focus-visible), .tabs__control.btn.custom-select__item:hover:not(.focus-visible) {
  background: rgba(255, 255, 255, 0);
}
@media (max-width: 1563px) {
  .tabs__control.btn {
    font-size: 18px;
  }
}
.tabs__select.custom-select {
  padding: 0;
  margin: 0;
  width: 100%;
}
.tabs__select.custom-select.is-open .custom-select__icon {
  transform: rotate(270deg);
}
.tabs .custom-select__button {
  display: none;
}
@media (max-width: 767px) {
  .tabs .custom-select__button {
    display: flex;
    border: 1px solid #002940;
    border-bottom: none;
    border-radius: 0;
    height: auto;
    min-height: 48px;
  }
  .tabs .custom-select__button:focus {
    border: 1px solid #002940;
    border-bottom: none;
  }
}
@media (max-width: 767px) {
  .tabs .custom-select__text {
    color: #002940;
    font-size: 12px;
    font-weight: 700;
    line-height: 110%;
    text-transform: uppercase;
  }
}
.tabs .custom-select__icon {
  width: auto;
  height: auto;
  background-color: rgba(255, 255, 255, 0);
  clip-path: none;
  transform: rotate(90deg);
}
.tabs .custom-select__icon svg {
  width: 15px;
  height: 15px;
  stroke: #002940;
}
@media (max-width: 767px) {
  .tabs .custom-select__item {
    font-size: 12px;
    padding: 5px 10px;
    line-height: 110%;
    min-height: 48px;
    align-items: center;
    width: 100%;
    border-bottom: none;
    border-left: none;
    border-right: none;
  }
}
.tabs .custom-select__list {
  position: static;
  max-height: none;
  visibility: visible;
  opacity: 1;
  transform: none;
  border: none;
  display: flex;
  width: 100%;
  margin: 0 0 20px 0;
  padding: 0;
  list-style: none;
  overflow: auto;
  scrollbar-width: auto;
  scrollbar-color: #002940 #f9f9fd;
}
@media (max-width: 1563px) {
  .tabs .custom-select__list {
    margin-bottom: 10px;
  }
}
@media (max-width: 1220px) {
  .tabs .custom-select__list {
    padding-bottom: 5px;
  }
}
@media (max-width: 767px) {
  .tabs .custom-select__list {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transform: translateY(100%);
    border: 1px solid #002940;
    border-top: none;
    z-index: 2;
    padding-bottom: 0;
    margin: 0;
    bottom: 0;
    flex-direction: column;
  }
}
.tabs .custom-select__list::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.tabs .custom-select__list::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(60, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
}
.tabs .custom-select__list::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: #002940;
}
.tabs__content {
  transition: height 0.3s;
}
.tabs__element {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(296px, 1fr));
